.OrderEnd {
  max-width: 790px;
  position: relative;
  margin: 0 auto;
  text-align: center;
}

.OrderEnd h2 {
  font-size: 30px;
}

.Item {
  width: 296px;
  height: 150px;
  position: relative;
  margin: 0 auto;
}
.Item1 {
  left: 20px;
}
.Item2 {
  top: -20px;
  left: -10px;
}

.Item img {
  width: 100%;
}

.InnerText {
  position: absolute;
  text-align: center;
  width: 100%;
  color: #fff;
  padding-left: 30px;
  padding-top: 25px;
}
.ColorNum {
  font-size: 10px;
  line-height: 10px;
  margin-bottom: 5px;
}
.ItemName {
  font-size: 10px;
  line-height: 10px;
}

.CodeName {
  line-height: 14px;
  margin-top: 15px;
  margin-bottom: 5px;
  font-size: 14px;
}
.Hashtag {
  font-size: 10px;
  line-height: 10px;
}
.Size {
  margin-top: 15px;
  font-size: 10px;
  line-height: 10px;
}

.Productbox {
  margin: 50px 0;
  padding: 30px 0;
}

.Userbox {
}
.Userbox table {
  max-width: 400px;
  margin: 0 auto;
  font-size: 15px;
}
.Userbox table th {
  color: #999;
  font-weight: 400;
  width: 100px;
}
.Userbox table td {
  font-weight: bold;
}
.Userbox table th,
.Userbox table td {
  text-align: left;
  border-bottom: 1px solid #eee;
  padding: 15px 5px;
}
